;define("delay", function(){
	var mSerialCache = {}
	,	delayWork
	,	api
	;

	/*
	@desc
	 Array    callback .
	@params
	list =    Array.
	max =   
	delay =    (ms)
	callback =  .  return  false    .
	thisObj = callback  this   

	     
	index = (Option)  
	length = (Option)  
	*/
	delayWork = function(list, max, delay, callback, thisObj, index, length, workSerial){
		var iLen
		,	i
		,	iFullLen
		,	isLast = false
		,	bCallbackRet
		,	isFirst = false
		,	serial = 0
		,	date
		;

		//   
		if (index === undefined){
			if (Object.prototype.toString.call(list).slice(8, -1) !== "Array"){
				return false;
			}

			iFullLen = list.length;

			//     
			//   .
			if (iFullLen <= max){
				isLast = true;
			}

			iLen = max;
			index = 0;
			isFirst = true;
		}
		//    
		else if (index > 0){
			if (index >= length){
				return true;
			}

			// ..
			if ((index + max) >= length){
				isLast = true;
				iLen = length - index;
			}
			// ...
			else{
				iLen = max;
			}

			iFullLen = length;

			//callback.call(thisObj, list.slice(index, iLen), index, length);
		}
		// -_-???
		else{
			return false;
		}

		bCallbackRet = callback.call(thisObj, list.slice(index, index + iLen), index, iFullLen, isLast);

		if (bCallbackRet === false){
			return true;
		}

		if (isLast){
			return true;
		}

		if (isFirst){
			date = new Date();
			workSerial = date.getTime();
		}

		serial = setTimeout(function(){
			delayWork( list, max, delay, callback, thisObj, index+=max, iFullLen, workSerial );
		}, delay);

		mSerialCache[ workSerial ] = serial;

		return workSerial;
	};

	

	return function(list, max, delay){
		return function(callback, thisObj){
			var serial = delayWork(list, max, delay, callback, thisObj)
			,	api = {
				id: 0,
				stop: function(serial){
					if (serial === undefined){
						serial = this.id;
					}

					clearTimeout( mSerialCache[ serial ] );
				},
				stopAll: function(){
					var m = mSerialCache
					,	key
					;

					for(key in m){
						clearTimeout( m[ key ] );
					}
				},
				pause: function(time){

				}
			};// api [end]

			api.id = serial;

			return api;
		};// return [end]
	};
});